#todoItem{
    width: 250px;
}

#todoButton{
    width: 70px;
}

button{
    margin-bottom: 10px;
}

#div-item{
    background-color: white;
    margin-bottom: 5px;
    border-radius: 9px;
}

#div-item p{
    padding: 16px;
    width: 90%;
    overflow-wrap: break-word;
}

#div-item div{
    display: inline-flex;
    padding-left: 16px;
}

#button-check{
    height: 20px;
    width : 25px;
}

#div-item button{
    height: 25px;
    width: 90px;
    border-radius: 3px;
}

#button-edit{
    background-color: yellow;
    color: black;
    border: yellow 1px solid;
}

#button-edit:active{
    background-color: #7e7e00;
    border: #7e7e00 1px solid;
}

#button-delete{
    margin-left: 5px;
    background-color: firebrick;
    color: white;
    border: firebrick 1px solid;
}

#button-delete:active{
    background-color: darkred;
    border: darkred 1px solid;
}
